import React from 'react';

import BaseQRCode from 'qrcode.react';

const QRCode = ({size}) => <div className='container'>
    <BaseQRCode value={window.location.href} size={size}/>
    <style jsx>{`
        .container {
            transform-origin: top right;
            transform: scale(0.25);
            position: absolute;
            top: 0;
            right: 0;

            z-index: 1000;

            display: none;
        }

        @media print {
            .container {
                display: block;
            }
        }
    `}</style>
</div>;

export default QRCode;
